<template>
    <!--  start-->
  <el-dialog
    title=""
    width="700px"
    :close-on-click-modal="false"
    :close-on-press-escape="false"
    :show-close="false"
    :append-to-body="true"
    :visible.sync="isShowCropper"
  >
    <!--  start -->
    <div class="vue-cropper-box">
      <!--  start -->
      <div class="vue-cropper-content">
        <div
          v-loading="loading"
          class="show-cropper"
          element-loading-text=""
          element-loading-spinner="el-icon-loading"
          element-loading-background="rgba(0, 0, 0, 0.8)"
        >
          <VueCropper
            ref="cropper"
            :img="options.img"
            :output-size="options.size"
            :output-type="options.outputType"
            :info="options.info"
            :center-box="options.centerBox"
            :can-scale="options.canScale"
            :auto-crop="options.autoCrop"
            :auto-crop-width="size[0]"
            :auto-crop-height="size[1]"
            :fixed-box="true"
            :fixed="true"
            :fixed-number="size"
            :original="options.original"
            :full="options.full"
            @imgLoad="imgLoad"
          />
        </div>
      </div>
      <!--  end -->

      <!--  start-->
      <div class="vue-cropper-bottom">
        <p class="vue-cropper-bottom-item">
          <el-tooltip
            class="item"
            effect="dark"
            content=""
            placement="bottom"
          >
            <i
              class="el-icon-zoom-in scale"
              @click="changeScale(1)"
            />
          </el-tooltip>
        </p>
        <p class="vue-cropper-bottom-item">
          <el-tooltip
            class="item"
            effect="dark"
            content=""
            placement="bottom"
          >
            <i
              class="el-icon-zoom-out scale"
              @click="changeScale(-1)"
            />
          </el-tooltip>
        </p>
        <p class="vue-cropper-bottom-item">
          <el-tooltip
            class="item"
            effect="dark"
            content="90"
            placement="bottom"
          >
            <i
              class="el-icon-refresh-left scale"
              @click="rotateLeft"
            />
          </el-tooltip>
        </p>
      </div>
      <!--  end-->
    </div>
    <!--  end -->

    <span
      slot="footer"
      class="dialog-footer"
    >
      <el-button
        v-if="isShowCropper"
        @click="cancelCubeImg"
      ></el-button>
      <el-button
        v-if="isShowCropper"
        type="danger"
        :disabled="loading"
        :loading="isLoading"
        @click="onCubeImg"
      ></el-button>
    </span>
  </el-dialog>
  <!--  end-->
</template>

<script>
import { VueCropper } from 'vue-cropper'

export default {
  name: 'vue-cropper',
  components: { VueCropper },
  props: {
    size: {
      type: Array,
      default: () => {
        return [670, 471]
      }
    }
  },
  data() {
      return {
        isCompress: false, // 
        compress: 0.8, // 
        isShowCropper: false,
        options: {
          img: '',
          info: true,
          size: 1,
          outputType: 'png', // 
          centerBox: true, // 
          canScale: true, // 

          autoCrop: true, // ,  

          original: false, // 
          full: true // 
        },
        fileName: null,

        loading: false,
        isLoading: false,
        previews: {}
    }
  },
  methods: {
    // 
    init() {
      this.isShowCropper = true
      this.loading = true
    },

    // 
    imgLoad(data) {
      this.loading = false
      this.isLoading = false
    },

    // 
    changeScale(val) {
      this.$refs.cropper.changeScale(val)
    },

    // 
    rotateLeft() {
      this.$refs.cropper.rotateLeft()
    },

    // 
    onCubeImg(file) {
      this.isLoading = true
      this.$refs.cropper.getCropData(data => {
        this.isLoading = false
        this.isShowCropper = false
        this.options.img = ''

        let file = null
        if (this.isCompress) { // 
          const img = new Image()
          img.src = data
          img.onload = () => {
            const _data = this.onImgCompression(img)
            file = this.dataURLtoFile(_data, this.fileName)
            // console.log('-:', (file.size / 1024).toFixed(2), 'kb', '', this.compress)
            this.$emit('confirm', file)
          }
        } else {
          file = this.dataURLtoFile(data, this.fileName)
          // console.log('-:', (file.size / 1024).toFixed(2), 'kb')
          this.$emit('confirm', file)
        }
      })
    },

    // base64file
    dataURLtoFile(dataurl, filename) {
      var arr = dataurl.split(','); var mime = arr[0].match(/:(.*?);/)[1]
      var bstr = atob(arr[1]); var n = bstr.length; var u8arr = new Uint8Array(n)
      while (n--) {
        u8arr[n] = bstr.charCodeAt(n)
      }
      return new File([u8arr], filename, { type: mime })
    },

    // 
    onImgCompression(img) {
      const canvas = document.createElement('canvas')
      const ctx = canvas.getContext('2d')
      // eslint-disable-next-line no-unused-vars
      const initSize = img.src.length
      const width = img.width
      const height = img.height
      canvas.width = width
      canvas.height = height
      // 
      ctx.fillStyle = '#fff'
      ctx.fillRect(0, 0, canvas.width, canvas.height)
      ctx.drawImage(img, 0, 0, width, height)
      // 
      const compress = this.compress || 0.8 // 
      return canvas.toDataURL('image/jpeg', compress)
    },

    // 
    cancelCubeImg() {
      this.isShowCropper = false
      this.$message.info('')
    }
  },
}
</script>

<style lang="scss" scoped>
.vue-cropper-box {
  width: 100%;
  height: 450px;
  .vue-cropper-content {
    width: 100%;
    height: 90%;
    padding-bottom: 20px;
    text-align: center;
    display: flex;
    justify-content: space-between;

    .show-cropper {
      // width: 50%;
      width: 100%;
      height: 100%;
    }
    .show-preview {
      width: 50%;
      height: 100%;
      margin: 5px;
      overflow: hidden;
    }
  }
}

  .vue-cropper-bottom {
    display: flex;
    flex-flow: wrap row;
    // justify-content: space-between;
    align-items: center;
    &-item {
      margin-right: 15px;
    }
    .scale {
      font-size: 24px;
      color: dodgerblue;
      vertical-align: middle;
      &:hover {
        color: mediumseagreen;
        box-shadow: 0 0 2px 1px rgba(0, 140, 186, 0.5);
      }
    }
  }
</style>


